import { useState } from "react";
import { ItemsTable } from "../components";

const BasketContainer = () => {
  const [items, setItems] = useState();
  const result = items.reduce(
    (previousValue, currentItem) =>
      previousValue + currentItem.price * currentItem.count,
    0
  );
  const EmptyTemplate = (
    <div className="empty-text">      </div>
  );
  const Footer = (
    <div className="result-panel">
      <span>
         : <span className="value">{result} .</span>
      </span>
      <button></button>
    </div>
  );

  const handleRemoveItem = (id) => {
    setItems(items.filter((item) => item.id !== id));
  };

  const handleIncreaceCount = (id) => {
    setItems(
      items.map((item) => {
        if (item.id === id) {
          item.count++;
        }
        return item;
      })
    );
  };

  const handleDecreaceCount = (id, count) => {
    if (count < 2) {
      handleRemoveItem(id);
    } else {
      setItems(
        items.map((item) => {
          if (item.id === id) {
            item.count--;
          }
          return item;
        })
      );
    }
  };

  return (
    <>
      <h1></h1>
      {items.length ? (
        <ItemsTable
          items={items}
          removeItem={handleRemoveItem}
          increaceCount={handleIncreaceCount}
          decreaceCount={handleDecreaceCount}
        />
      ) : (
        EmptyTemplate
      )}
      {!!items.length && Footer}
    </>
  );
};

export default BasketContainer;
